* {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: rgb(234, 243, 243);
}
.container {
  padding: 10px;
}
form {
  margin-top: 10px;
}
.form-container {
  max-width: 600px;
  background-color: white;
  margin: auto;
  margin-top: 50px;
  box-shadow: gray 2px 2px 5px;
  padding: 15px;
  text-align: center;
  border-radius: 5px;
}
.header {
  width: 100%;
  font-size: 28px;
  font-weight: 700;
  color: rgb(44, 42, 42);
  margin-top: 5px;
}
.input-group {
  width: 100%;
  margin-top: 10px;
  margin: auto;
}
.input-group input {
  width: 70%;
  border-color: transparent;
  padding: 7px;
  /* border: 1px solid rgb(173, 166, 166); */
  background-color: rgb(223, 248, 248);
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.input-group .btn {
  width: 30%;
  border-color: transparent;
  padding: 7px;
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  text-transform: capitalize;
  background-color: teal;
  font-weight: 600;
  letter-spacing: 2px;
  color: white;
}
.clearAll {
  margin-top: 15px;
  text-align: center;
  width: 100%;
}
.clearAll .btn {
  width: 100px;
  padding: 5px;
  color: rgb(220, 245, 245);
  border-color: transparent;
  font-size: 18px;
  border-radius: 5px;
  background-color: teal;
}
.todolist-container {
  width: 100%;
  /* display: inline-block; */
  text-align: center;
}
.todoitem {
  width: 260px;
  height: 230px;
  background-color: inherit;
  /* display: inline-block; */
  margin: auto 20px 10px;
  /* object-fit: cover; */
  position: relative;
  overflow: hidden;
  box-shadow: rgb(120, 145, 145) 4px 4px 10px;
}
.itemcontainer {
  display: inline-block;
  margin: 10px;
}
.todoitem img {
  width: 500px;
  height: 250px;
  position: absolute;
  left: -138px;
  top: -11px;
  z-index: 1;
  object-fit: cover;
}
.todoitem p {
  z-index: 2;
  position: absolute;
  padding: 16px;
  top: 24px;
  text-align: left;
  font-size: 18px;
  font-weight: bold;
  text-transform: capitalize;
}
.btnContainer {
  width: 100%;
  height: 10px;
  background-color: inherit;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  padding: 3px;
  position: absolute;
  z-index: 3;
}
.done {
  text-decoration: line-through;
  color: gray;
}

.btn2 {
  padding: 3px;
  border-color: transparent;
  margin-left: 20px;
  background-color: inherit;
  font-size: 18px;
}
.comment {
  color: rgb(3, 158, 3);
}
.edit {
  color: rgb(236, 224, 57);
}
.delet {
  color: rgb(146, 13, 13);
}
.alertshow {
  font-size: 12px;
  text-align: center;
  margin-top: 10px;
  border-radius: 5px;
  padding: 2px;
  color: white;
  text-transform: capitalize;
}
.danger {
  background-color: rgb(253, 76, 76);
}
.alert {
  background-color: rgb(253, 76, 76);
}
.success {
  background-color: rgb(101, 228, 118);
}

@media only screen and (max-width: 350px) {
  .input-group input {
    width: 100%;
    margin: auto;
    display: block;
    border-radius: 5px;
  }
  .input-group .btn {
    width: 100px;
    margin: auto;
    margin-top: 10px;
    display: block;
    border-radius: 5px;
  }
  .todoitem {
    width: 200px;
    height: 180px;
    background-color: inherit;
    /* display: inline-block; */
    margin: auto 20px 10px;
    object-fit: cover;
    position: relative;
  }
  .todoitem p {
    z-index: 2;
    position: absolute;
    padding: 12px;
    top: 24px;
    text-align: left;
    font-size: 18px;
  }
  .clearAll .btn {
    width: 70px;
    padding: 5px;
    font-size: 13px;
  }
}
